.Wrapper {
  width: 100%;
  margin: 0 auto;
  padding: 0 16px;
  min-width: 360px;
  box-sizing: border-box;

  @media(min-width: 360px){
    padding: 0 16px;
  }

  @media(min-width: 768px){
    padding: 0 32px; 
  }

  @media(min-width: 1024px){
    padding: 0 60px;
  }

  @media(min-width: 1170px){
    padding: 0;
  }
}